import React, {PureComponent} from 'react';
import PropTypes from 'prop-types';
import {Avatar, Button, ScreenSpinner, Div, Text} from "@vkontakte/vkui";
import {Dropdown} from "@vkontakte/vkui/dist/unstable";
import {
    Icon28Cards2Outline,
    Icon28ClockOutline,
    Icon28CrownOutline, Icon28FilmStripOutline,
    Icon28GraphOutline,
    Icon28StorefrontOutline
} from "@vkontakte/icons";
import {Icon28Play} from "@vkontakte/icons";
import {convertMsToNormalTime, decOfNum, isPlatformDesktop, shortIntegers, shortIntegersK} from "../../js/utils";
import {Icon24Like} from "@vkontakte/icons";
import {getVKUsers, vk_local_users} from "../../js/drawerapp/utils";
import {MODAL_CARD_PLAY_MODE, app_version, MODAL_CARD_OUTDATED_VERSION} from "./Drawing";
import {Icon12Chevron} from "@vkontakte/icons";
import {Icon28AddCircleOutline} from "@vkontakte/icons";
import {Icon28ShoppingCartOutline} from "@vkontakte/icons";
import eruda from 'eruda';
import bridge from "@vkontakte/vk-bridge";

const isDesktop = isPlatformDesktop();
let globalCanvasTicker;

export class Main extends PureComponent {

    constructor(props) {
        super(props);

        this.state = {
            isShowGlobalCanvas: props.t.state.isShowGlobalCanvas,
            canvas_status: {status: false},
            globalCanvasShowTimer: false
        };

        props.t.socket.unsubscribe('changeOnline');
        props.t.socket.subscribe('changeOnline', r => {
            props.t.setState({online: r.count});
            this.forceUpdate();
        });

        this.goToStats = this.goToStats.bind(this);
        this.goToFeed = this.goToFeed.bind(this);
    }

    goToStats() {
        const {t} = this.props;
        t.setPopout(<ScreenSpinner/>);
        t.socket.call('users.getTop', {type: 'pictures'}, r => {
            t.setState({users_topPictures: r.response});
            let user_ids = r.response.map(value => value.id);

            t.socket.call('users.getTop', {type: 'guessed_pictures'}, r => {
                t.setState({users_topGuessedPictures: r.response});
                user_ids = [...user_ids, ...r.response.map(value => value.id)];

                t.socket.call('users.getTop', {type: 'likes'}, async r => {
                    t.setState({users_topLikes: r.response});
                    user_ids = [...user_ids, ...r.response.map(value => value.id)];
                    await getVKUsers(user_ids);

                    t.setPopout(null);
                    t.go('stats');
                });
            });
        });
    }

    goToFeed() {
        const {t} = this.props;
        t.setPopout(<ScreenSpinner/>);
        t.socket.call('pictures.getRandomLikes', {limit: 10}, r => {
            t.setState({randomGamesLike: r.response});
            let user_ids = r.response.map(value => value.drawerId);

            t.socket.call('pictures.getLikesTop', {limit: 10}, async r => {
                t.setState({topLikes: r.response});
                user_ids = [...user_ids, ...r.response.map(value => value.drawerId)];
                await getVKUsers(user_ids);

                t.setPopout(null);
                t.go('likes');
            });
        });
    }

    componentDidMount() {
        const {socket} = this.props.t;
        socket.call('canvas.getStatus', {}, async r => {
            const
                {response} = r,
                timeLost = response.start - Date.now(),
                globalCanvasShowTimer = (timeLost <= 24 * 60 * 60 * 1000) && (timeLost > 0)
            ;

            if (globalCanvasShowTimer) {
                globalCanvasTicker = setInterval(() => {
                    if (response.start - Date.now() <= 0) {
                        this.setState({globalCanvasShowTimer: false});
                        clearInterval(globalCanvasTicker);
                    }
                    this.forceUpdate();
                }, 1000);
            }

            this.setState({canvas_status: response, globalCanvasShowTimer});
        });
    }

    componentWillMount() {
        clearInterval(globalCanvasTicker);
    }

    render() {
        const
            {t} = this.props,
            {state, _tags} = t,
            {gamesAsWinner, gamesAsDrawer, top_number, online, user} = state,
            {lvl, pictures, successfulPictures, guessedPictures, likes} = user,
            offset = [
                Math.min(t.state.colors.length - 3, Math.max(lvl, 4) - 4),
                Math.min(t.state.colors.length, Math.max(lvl, 3) + 3)
            ],
            colors = t.state.colors.slice(offset[0], offset[1]),
            maxLvl = Math.min(lvl, t.state.colors.length),

            {isShowGlobalCanvas, canvas_status, globalCanvasShowTimer} = this.state,

            gc_time = globalCanvasShowTimer ? convertMsToNormalTime(canvas_status.start - Date.now()).str : {}
        ;

        if (offset[0] > t.state.colors.length - 6) {
            offset[0] = t.state.colors.length - 6;
        }

        return (
            <div className='Main'>
                <div
                    className='app_version'
                    onClick={async () => {
                        if (state.app_version !== app_version) {
                            t.setActiveModal(MODAL_CARD_OUTDATED_VERSION);
                        }
                        await t.setState({__v_clicks: state.__v_clicks === 4 ? 4 : (state.__v_clicks + 1)});
                        this.forceUpdate();
                        if (state.__v_clicks === 3) {
                            eruda.init();
                        }
                    }}
                >v{app_version} {state.app_version !== app_version && <span>(неактуальная)</span>}
                </div>
                <div className='User_Header'>
                    <Avatar src={t.state.vk_user.photo_100} size={48} shadow={false}/>
                    <div className='UserTags'>
                        {
                            _tags().map(value =>
                                    user[value[0]] && <Dropdown
                                        action='hover'
                                        content={
                                            <Div>
                                                <Text>{value[2]}</Text>
                                            </Div>
                                        }
                                    >
                                        {React.cloneElement(value[1], {onClick: () => t.setActiveModal(value[0])})}
                                    </Dropdown>
                            )
                        }
                    </div>
                </div>
                <h3>Привет, {t.state.vk_user.first_name}</h3>
                <h1>Твой профиль</h1>
                <div className='Colors__Container'>
                    <div className='Gradient_Opacity'/>
                    {
                        t.state.colors.map((value, index) =>
                            <div
                                className='Color'
                                key={`Color__${index}`}
                                style={{
                                    display: (offset[0] > index || offset[1] < index) && 'none',
                                    marginLeft: index > 0 && 12,
                                    background: index + 1 === maxLvl && 'linear-gradient(156.04deg, #F05F88 0%, #FBD20A 100%)',
                                    border: index + 1 === maxLvl && 'none'
                                }}
                            >
                                <span>{index + 1}</span>
                                <div
                                    style={{background: typeof value === 'object' ? `conic-gradient(from 180deg at 50% 50%, ${value.join(', ')})` : value}}/>
                            </div>
                        )
                    }
                </div>
                <p>Отгадывай и рисуй картины, чтобы разблокировать новые цвета</p>
                <div className='PlayCards__Container'>
                    <div className='PlayCards_Lobbies__Container'>
                        <div
                            className='PlayCard GlobalLobby'
                            onClick={() => t.go('search')}
                        >
                            <Icon28Play/>
                            <span>Играть</span>
                        </div>
                        <div
                            className='PlayCard SelfLobby'
                            onClick={() => t.go('friends')}
                        >
                            <Icon28AddCircleOutline/>
                            <span>Своя игра</span>
                        </div>
                    </div>
                    <div
                        style={{
                            display: (canvas_status.end !== 0 && Date.now() >= canvas_status.end) ? 'none' :
                                (globalCanvasShowTimer ? '' : (canvas_status.status ? '' : ((canvas_status.admins_allow && user.admin) ? '' : 'none')))
                        }}
                        className='PlayCard GlobalCanvasButton'
                        onClick={async () => {
                            if (!globalCanvasShowTimer) {
                                bridge.send('VKWebAppStorageSet', {key: 'canvas_new_mode', value: '1'});
                                t.setState({isShowGlobalCanvas: false});
                                this.setState({isShowGlobalCanvas: false});
                                t.go('global_canvas');
                            }
                        }}
                    >
                        {
                            globalCanvasShowTimer &&
                            <div className='TimeCounter'>
                                <Icon28ClockOutline/>
                                <span>{gc_time.hours}:{gc_time.minutes}:{gc_time.seconds}</span>
                            </div>
                        }
                        <Icon28CrownOutline/>
                        <span>Общий холст</span>
                        {
                            isShowGlobalCanvas && <div className='Point_New_Notify'></div>
                        }
                    </div>
                    {
                        isDesktop && <p className='OnlineText'>Онлайн: {online}</p>
                    }
                </div>
                <div className='PlayerPics__Container'>
                    <div className='Card_Button_Stretch'>
                        <div className='Images'>
                            {[1, 0, 2].map((value, index) => <div num={index} key={`div__${index}`}>{
                                gamesAsDrawer[value] && <img alt='img' src={gamesAsDrawer[value]['Picture.url']}/>
                            }</div>)}
                        </div>
                        <div
                            className='Card_Text__Container'
                            onClick={() => {
                                if (gamesAsDrawer.length > 0)
                                    t.go('gallery')
                            }}
                        >
                            <div>
                                <span>Мои работы</span>
                                <span>{decOfNum(successfulPictures, ['успешная', 'успешные', 'успешных'])}</span>
                            </div>
                            <div>{decOfNum(pictures, ['картина', 'картины', 'картин'])}</div>
                        </div>
                    </div>
                    <div className='Card_Button_Stretch'>
                        <div className='Images'>
                            {[1, 0, 2].map((value, index) => <div num={index} key={`div__${index}`}>{
                                gamesAsWinner[value] && <img alt='img' src={gamesAsWinner[value]['Picture.url']}/>
                            }</div>)}
                        </div>
                        <div
                            className='Card_Text__Container'
                            onClick={() => t.go('rating')}
                        >
                            <div>
                                <span>Отгаданные работы</span>
                                <span>{top_number && `${shortIntegersK(top_number)} место`}</span>
                            </div>
                            <div>{decOfNum(guessedPictures, ['картина', 'картины', 'картин'])}</div>
                        </div>
                    </div>
                </div>
                <div className='SubCards__Container'>
                    {
                        [
                            [<Icon28Cards2Outline/>, 'Лента', () => this.goToFeed()],
                            [<Icon28GraphOutline/>, 'Статистика', () => this.goToStats()],
                            [<Icon28StorefrontOutline/>, 'Магазин', () => t.go('shop')]
                        ].map((value, index) =>
                            <div
                                key={`SubCard_${index}`}
                                onClick={() => value[2]()}
                                style={{
                                    marginLeft: index !== 0 && 8
                                }}
                            >
                                {value[0]}
                                <span>{value[1]}</span>
                            </div>
                        )
                    }
                </div>
                {
                    !isDesktop && <p className='OnlineText'>Онлайн: {online}</p>
                }
                {false && <React.Fragment>
                    <div className='Cards__Container'>
                        <div onClick={() => {
                            if (gamesAsDrawer.length > 0) {
                                t.go('gallery')
                            }
                        }}>
                            <span className='Top'>
                                {decOfNum(successfulPictures, ['успешная', 'успешные', 'успешных'])}
                            </span>
                            <div className='Images'>
                                {[1, 0, 2].map((value, index) => <div num={index} key={`div__${index}`}>{
                                    gamesAsDrawer[value] && <img alt='img' src={gamesAsDrawer[value]['Picture.url']}/>
                                }</div>)}
                            </div>
                            <div className='Texts'>
                                <span>Галерея</span>
                                <span>{decOfNum(pictures, ['картина', 'картины', 'картин'])}</span>
                            </div>
                        </div>

                        <div onClick={() => t.go('rating')}>
                            <span className='Top'>{top_number && `Ты на ${shortIntegersK(top_number)} месте`}</span>
                            <div className='Images'>
                                {[1, 0, 2].map((value, index) => <div num={index} key={`div__${index}`}>{
                                    gamesAsWinner[value] && <img alt='img' src={gamesAsWinner[value]['Picture.url']}/>
                                }</div>)}
                            </div>
                            <div className='Texts'>
                                <span>Отгадано</span>
                                <span>{decOfNum(guessedPictures, ['картина', 'картины', 'картин'])}</span>
                            </div>
                        </div>
                    </div>
                    <div className='Cards2__Container'>
                        <div onClick={async () => {
                            this.goToStats();
                        }}>
                            <span>статистика</span>
                            <Icon12Chevron/>
                        </div>
                        <div onClick={async () => {
                            this.goToFeed();
                        }}>
                            <span>оценить картины</span>
                            <Icon12Chevron/>
                        </div>
                    </div>
                    <div className='CardButton' onClick={async () => {
                        t.go('shop');
                    }}>
                        {isPlatformDesktop() && <Icon28ShoppingCartOutline/>}
                        <span>магазин</span>
                        {!isPlatformDesktop() && <Icon12Chevron/>}
                    </div>
                    {
                        !isPlatformDesktop() && <div style={{height: 111, width: 1}}/>
                    }
                    <div className='PlayContainer'>
                        <div>
                            <div className='ButtonPlayGlobal' onClick={() => t.go('search')}>
                                <Icon28Play/>
                            </div>
                            <div className='ButtonPlayFriends' onClick={() => t.go('friends')}>
                                <Icon28AddCircleOutline/>
                            </div>
                        </div>
                        <p>Онлайн: {online}</p>
                    </div>
                    <div className='MainTint'/>
                </React.Fragment>}
            </div>
        )
    }
}

Main.defaultProps = {};

Main.propTypes = {
    t: PropTypes.object
};

export default Main;