import React from 'react';
import bridge from '@vkontakte/vk-bridge';
import '../../css/Drawing/Global.css';

import {
    adAppApi,
    get,
    getRandomInts,
    getUrlParams,
    isPlatformDesktop,
    isPlatformIOS,
    loadFonts,
    openUrl
} from "../../js/utils";
import {vk_local_users, getVKUsers} from "../../js/drawerapp/utils";
import {
    Button,
    ModalCard,
    ModalPage,
    ModalRoot,
    Panel,
    ScreenSpinner,
    Snackbar,
    View,
    ModalPageHeader,
    PanelHeaderButton,
    SliderSwitch,
    Input,
    IconButton,
    SegmentedControl,
    Div,
    SplitLayout,
    SplitCol, useAdaptivity, ViewWidth
} from '@vkontakte/vkui';
import {
    Icon16Link, Icon16Share,
    Icon20AchievementCircleFillBlue,
    Icon20FireCircleFillRed,
    Icon20RecentCircleFillYellow, Icon20ThumbsUpCircleFillGreen, Icon20StoryReplyCircleFillViolet, Icon24Dismiss,
    Icon28ChevronRightOutline,
    Icon28ClockCircleFillGray, Icon28CopyOutline, Icon28SlidersOutline,
    Icon56CakeCircleFillPurple,
    Icon56ErrorTriangleOutline,
    Icon56ServicesOutline, Icon20CancelCircleFillRed
} from "@vkontakte/icons";

import fetch from 'node-fetch';

import {registerFont, createCanvas, loadImage} from 'canvas';
import request from "request";

import {ReactComponent as IconOnboard} from "../../assets/drawing/icons/Icon.svg";
import {ReactComponent as IconError} from "../../assets/drawing/icons/IconError.svg";

import Main from "./Main";
import Gallery from "./Gallery";
import Rating from "./Rating";
import Search from "./Search";
import Game from "./Game";
import Result from "./Result";
import Placeholder from "./Placeholder";

import Socket from '../../js/socket';
import Likes from "./Likes";
import PictureInfo from "./PictureInfo";
import Author from "./Author";
import Tags from "./Tags";
import Friends from "./Friends";
import FriendLobby from "./FriendLoby";
import Stats from "./Stats";
import Shop from "./Shop";
import GlobalCanvas from "./GlobalCanvas";
import GlobalCanvasInfo from "./GlobalCanvasInfo";

import (`../../css/Drawing/${window.location.search.length > 0 && JSON.parse('{"' + decodeURI(window.location.search.substring(1)).replace(/"/g, '\\"').replace(/&/g, '","').replace(/=/g, '":"') + '"}').vk_platform === 'desktop_web' ? 'Desktop' : 'Mobile'}.css`);

const
    socket = new Socket()
;
let
    bannersInterval,
    connectInterval
;

export const
    app_version = '1.1.5',

    MODAL_CARD_PLAY_MODE = 'play-mode',
    MODAL_CARD_NEW_UPDATE = 'new-update',
    MODAL_CARD_OUTDATED_VERSION = 'outdated-version',

    MODAL_PAGE_FRIEND_LOBBY_SETTINGS = 'friend-lobby-settings'
;

class Drawing extends React.Component {

    constructor(props) {
        super(props);

        this.state = {
            isNotApp: false,

            activePanel: 'main',
            history: ['main'],

            activeModal: null,
            modalHistory: [],

            colors: [
                ['#ff0000', '#ff9a00', '#d0de21', '#4fdc4a', '#3fdad8', '#2fc9e2', '#1c7fee', '#ba0cf8', '#fb07d9', '#ff0000'],
                '#000000',
                '#FFFFFF',
                '#E63902',
                '#EA5A03',
                '#FA8A00',
                '#FBB101',
                '#FDE401',
                '#85B201',
                '#019317',
                '#019C8B',
                '#035AA2',
                '#00247A',
                '#500A6D',
                '#A9236B',
                '#964B00',
                '#F57C7C'
            ],

            __v_clicks: 0,

            online: 0,

            drawerId: 0,
            lobbyId: 0,

            isOnlineDrawing: true,

            bannersCount: 9,
            banners: [],
            defaultBannerData: {
                title: 'Заголовок',
                domain: 'vk.com',
                trackingLink: 'https://vk.com',
                ctaText: 'Перейти',
                advertisingLabel: 'Реклама',
                iconLink: 'https://sun9-7.userapi.com/c846420/v846420985/1526c3/ISX7VF8NjZk.jpg',
                description: 'Описание рекламы',
                ageRestrictions: "14+",
                statistics: [
                    {url: '', type: 'playbackStarted'},
                    {url: '', type: 'click'}
                ]
            },
            connected: false,
            dataUpdated: false,

            author_works: [],

            subGameInfo: {},

            watchedShopItems: [],
            boughtShopItems: [],

            user: {},
            friend_lobby: {}
        };

        this.componentDidMount = this.componentDidMount.bind(this);
        this.updateData = this.updateData.bind(this);
        this.back = () => {
            const {history, popout, activeModal} = this.state;

            if (popout !== null && popout !== undefined)
                return;

            if (activeModal !== null) {
                this.setState({activeModal: null});
                window.history.pushState({pop: 'popout'}, 'Title');
                return;
            }

            if (history.length > 1) {
                if (history[history.length - 1] === 'search') {
                    socket.call('games.search');
                    this.setState({
                        snackbar: <Snackbar onClose={() => this.setState({snackbar: null})}>
                            Поиск игры отменён
                        </Snackbar>
                    });
                }

                if (history[history.length - 1] === 'friend_lobby') {
                    socket.call('friends.leaveLobby', {owner_id: this.state.friend_lobby.owner_id});
                }

                history.pop();
                this.setState({activePanel: history[history.length - 1], history});
                if (history[history.length - 1] !== 'search')
                    this.setState({snackbar: null});
            } else {
                bridge.send('VKWebAppClose', {status: 'success'});
            }
        };
        this.go = (panel) => {
            const {history, activeModal} = this.state;
            if (activeModal !== null) {
                this.setState({activeModal: null, modalHistory: []});
            }
            if (history[history.length - 1] !== panel) {
                history.push(panel);
                window.history.pushState({activePanel: panel}, 'Title');
                this.setState({activePanel: panel, history, snackbar: null});
            }
        };
        this.setActivePanel = (panel, history = []) => {
            this.setState({activePanel: panel, history: [...history, panel], snackbar: null});
        };
        this.setActiveModal = (activeModal = null) => {
            let {modalHistory} = this.state;

            if (activeModal === null) {
                modalHistory = [];
            } else if (modalHistory.indexOf(activeModal) !== -1) {
                modalHistory = modalHistory.splice(
                    0,
                    modalHistory.indexOf(activeModal) + 1
                );
            } else {
                modalHistory.push(activeModal);
            }

            this.setState({
                activeModal,
                modalHistory,
            });
        };
        this.setPopout = (popout) => {
            this.setState({popout});
        };
        this.socket = socket;
    }

    async componentDidMount() {
        loadFonts();

        window.addEventListener('popstate', e => {
            e.preventDefault();
            this.back();
        });

        bridge.subscribe(async ({detail: {type, data}}) => {
            if (type !== undefined) {
                if (type === 'vk-connect' && !data) {
                    if (!this.state.isNotApp) {
                        await this.setState({isNotApp: true});
                        document.body.style.width = '660px';
                        document.body.style.height = '600px';
                        document.body.style.position = 'absolute';
                        document.body.style.top = '50%';
                        document.body.style.left = '50%';
                        document.body.style.transform = 'translate(-50%, -50%)';
                        console.log('Log in as not app version');
                        this.componentDidMount();
                    }
                } else {
                    console.log(type, data);
                }
            }
            if (type === 'VKWebAppUpdateConfig') {
                const schemeAttribute = document.createAttribute('scheme');
                schemeAttribute.value = data.scheme ? data.scheme === 'client_light' ? 'bright_light' : data.scheme : 'bright_light';
                document.body.attributes.setNamedItem(schemeAttribute);

                const
                    windowHeight = document.documentElement.clientHeight,
                    windowWidth = document.documentElement.clientWidth,
                    scale = 100 / 812 * (windowHeight > 0 ? windowHeight : 812) / 100
                ;
                document.body.style.setProperty('--scale-value', scale);
                console.log(`windowHeight = ${document.documentElement.clientHeight}`);

                document.body.setAttribute('_width', windowWidth);
                document.body.setAttribute('_height', windowHeight);
            } else if (type === 'VKWebAppViewRestore') {
                this.setState({popout: null});
            }
        });

        const
            {isNotApp} = this.state,
            isShowOnboard = !isNotApp && (await bridge.send('VKWebAppStorageGet', {keys: ['onboard']})).keys[0].value === '',
            isShowVkGroup = !isNotApp && (await bridge.send('VKWebAppStorageGet', {keys: ['vk_group1']})).keys[0].value === '',
            isShowUpdate = !isNotApp && (await bridge.send('VKWebAppStorageGet', {keys: ['update_01062022']})).keys[0].value === '',
            isShowGlobalCanvas = !isNotApp && (await bridge.send('VKWebAppStorageGet', {keys: ['canvas_new_mode']})).keys[0].value === '',
            vk_user = !isNotApp && await bridge.send('VKWebAppGetUserInfo'),
            vk_user_ = await getVKUsers([vk_user ? vk_user.id : getUrlParams().vk_user_id])
        ;
        await this.setState({isShowOnboard, isShowVkGroup, isShowGlobalCanvas, vk_user: vk_user || vk_user_[0]});

        bridge.send('VKWebAppEnableSwipeBack');

        this.connect();

        socket.subscribe('exit', r => {
            this.setState({placeholderText: r.title, banned: r.banned});
            this.setPopout(null);
            if (r.banned) {
                this.setState({placeholderNeed: 2});
                this.setActivePanel('banned');
            } else {
                this.setState({placeholderNeed: 1});
                this.setActivePanel('placeholder');
            }
            bridge.send('VKWebAppInit');
        });
        socket.subscribe('app_version', r => {
            this.setState({app_version: r.app_version});
        })
        socket.onConnectionError((e) => {
            console.error(e);
            this.setState({placeholderText: 'Не удалось подключиться к серверу.', placeholderNeed: 1});
            this.setPopout(null);
            this.setActivePanel('placeholder');
            bridge.send('VKWebAppInit');
        });
        socket.onDisconnect(() => {
            this.setPopout(null);
            if (this.state.placeholderNeed !== 2) {
                this.setState({placeholderText: ''});
                this.setActivePanel('placeholder');
            }
            bridge.send('VKWebAppInit');
        });

        await new Promise(resolve =>
            socket.onConnect(async () => {
                await this.setState({connected: true, placeholderNeed: 0});
                console.log('Connected');
                /*this.setState({test_game: true});*/
                this.setActivePanel('main');
                await this.updateData();

                if (isShowVkGroup && !isShowOnboard) {
                    setTimeout(() => {
                        this.go('group_vk');
                    }, 1000);
                } else if (isShowUpdate && !isShowOnboard) {
                    setTimeout(() => {
                        this.setActiveModal(MODAL_CARD_NEW_UPDATE);
                    }, 1000);
                } else if (!isShowOnboard) {
                    this.onChangeHash();
                }
                resolve(true);
            })
        );

        clearInterval(bannersInterval);
        bannersInterval =
            setInterval(() => {
                const {banners, bannersCount} = this.state;
                for (let i = 0; i < bannersCount; i++) {
                    bridge.send('VKWebAppGetAds', {}).then(data => {
                        if (banners[i] !== false)
                            banners[i] = data;
                    });
                }
                this.setState({banners});
            }, 3000);

        bridge.send('VKWebAppInit');

        adAppApi('stats.join');
    }

    connect() {
        socket.connect('https://draw.avocado.special.vk-apps.com', getUrlParams());
        setTimeout(() => {
            if (!this.state.dataUpdated) {
                this.updateData();
            }
            if (!this.state.connected) {
                clearInterval(connectInterval);
                connectInterval = setInterval(() => {
                    if (!this.state.connected) {
                        socket.connect('https://draw.avocado.special.vk-apps.com', getUrlParams());
                    } else {
                        clearInterval(connectInterval);
                    }
                }, 1000);
            }
        }, 1000);
    }

    async updateData() {
        await new Promise(resolve => {
            socket.call('users.getTopNumber', {}, r => {
                this.setState({top_number: r.response});
            });
            socket.call('users.getTop', {}, async r => {
                const
                    user_ids = r.response.map(value => value.id)
                ;
                await getVKUsers(user_ids);

                this.setState({top: r.response.map(value => ({...vk_local_users[value.id], ...value}))});
            });
            socket.call('users.get', {}, r => {
                this.setState({user: r.response});

                socket.call('games.getByDrawerId', {id: this.state.vk_user.id, limit: 10}, r => {
                    this.setState({gamesAsDrawer: r.response});
                    let user_ids = r.response.map(value => value.drawerId);

                    socket.call('games.getByWinnerId', {id: this.state.vk_user.id, limit: 10}, r => {
                        user_ids = [...user_ids, ...r.response.map(value => value.drawerId)];

                        getVKUsers(user_ids);
                        setTimeout(async () => {
                            await this.setState({gamesAsWinner: r.response, dataUpdated: true});
                            resolve(true);
                        }, 400);
                    });
                });
            })
        });

        this.setState({time: Date.now()});
        return true;
    }

    onChangeHash() {
        if (window.location.hash.startsWith('#')) {
            const
                hash = window.location.hash.substring(1),
                keys = [
                    {
                        key: 'author',
                        do: value => {
                            this.setPopout(<ScreenSpinner/>);
                            this.socket.call('users.getById', {id: value}, async r => {
                                if (r.response !== null) {
                                    await getVKUsers([value]);
                                    await this.setState({
                                        author: r.response
                                    });
                                    this.socket.call('games.getByDrawerId', {id: value}, async r => {
                                        await this.setState({
                                            author_works: r.response
                                        });
                                        setTimeout(() => {
                                            this.go('author');
                                            this.setPopout(null);
                                        }, 400);
                                    });
                                } else {
                                    this.setPopout(null);
                                }
                            });
                        }
                    },
                    {
                        key: 'picture',
                        do: async value => {
                            try {
                                const
                                    gameId = parseInt(value)
                                ;

                                this.setPopout(<ScreenSpinner/>);
                                this.socket.call('games.getById', {id: gameId}, async r => {
                                    if (r.response !== null) {
                                        const {drawerId} = r.response;
                                        await getVKUsers([drawerId]);
                                        await this.setState({
                                            gamesAsAuthor: [r.response],
                                            like_currentPictureSelected: 0,
                                            like_method: '',
                                            like_param: 'gamesAsAuthor',
                                            like_user_id: drawerId
                                        });
                                        setTimeout(() => {
                                            this.go('picture_info');
                                            this.setPopout(null);
                                        }, 400);
                                    } else {
                                        this.setPopout(null);
                                    }
                                });
                            } catch (e) {
                                console.error(e);
                            }
                        }
                    },
                    {
                        key: 'lobby',
                        do: value => {
                            this.setPopout(<ScreenSpinner/>);
                            this.socket.call('friends.joinLobby', {owner_id: parseInt(value), url: true}, async r => {
                                if (r.response) {
                                    await getVKUsers(r.response.members);
                                    await this.setState({
                                        friend_lobby: r.response
                                    });
                                    setTimeout(() => {
                                        this.go('friend_lobby');
                                        this.setPopout(null);
                                    }, 400);
                                } else {
                                    this.setPopout(null);
                                }
                            });
                        }
                    },
                ]
            ;

            for (const key of keys) {
                if (hash.startsWith(key.key)) {
                    key.do(hash.substring(key.key.length));
                }
            }
        }
    }

    _tags() {
        return [
            [
                'tag_interesting',
                <Icon20FireCircleFillRed/>,
                'Интересный автор',
                'Метка выдаётся при достижении 1000 лайков'
            ],
            [
                'tag_rater',
                <Icon20ThumbsUpCircleFillGreen/>,
                'Оценщик',
                'Оценить 100 картин'
            ],
            [
                'tag_agitator',
                <Icon20StoryReplyCircleFillViolet/>,
                'Агитатор',
                'Рассказать о своих работах друзьям (10 раз)'
            ],
            [
                'tag_rapidfire',
                <Icon20RecentCircleFillYellow/>,
                'Скорострел',
                'Метка выдаётся за картину, нарисованную и угаданную за 5 секунд'
            ],
            [
                'tag_turtle',
                <Icon28ClockCircleFillGray width={20} height={20}/>,
                'Черепаха',
                'Метка выдаётся за картину, нарисованную и угаданную за 85-90 секунд'
            ],
            [
                'tag_creative',
                <Icon20AchievementCircleFillBlue/>,
                'Креативный',
                'Метка выдаётся вручную администрацией, если та посчитает твои картины креативными'
            ],
            [
                'tag_banned',
                <Icon20CancelCircleFillRed/>,
                'Нарушитель',
                'Метка свидетельствует о нарушении правил игры'
            ],
        ];
    }

    _tag_modal(tag_name) {
        const tag_info = this._tags().find(value => value[0] === tag_name);
        return (
            <ModalCard
                key={tag_name}
                id={tag_name}
                onClose={() => this.setActiveModal(null)}
                icon={React.cloneElement(tag_info[1], {width: 56, height: 56})}
                header={tag_info[2]}
                subheader={tag_info[3]}
                actions={
                    <Button
                        size='l'
                        mode='gradient_blue'
                        onClick={() => this.go('tags')}
                    >
                        Подробнее
                    </Button>
                }
            ></ModalCard>
        );
    }

    isNonScrollingElement(element_id) {
        return ['global_canvas'].indexOf(element_id) > -1;
    }

    render() {
        const
            {
                activePanel, activeModal,
                popout, snackbar,

                isShowOnboard, isShowVkGroup,
                vk_user, user, banned,

                placeholderText,

                gamesAsWinner, gamesAsDrawer,

                drawerId, winnerId, lobbyId, gameId, word,
                like_picturesList, like_currentPictureSelected, like_method, like_param, like_user_id,

                isOnlineDrawing,

                friend_lobby,

                watchedShopItems, boughtShopItems
            } = this.state,
            showConnecting = !(vk_user && user && gamesAsWinner && gamesAsDrawer),

            modal = (
                <ModalRoot activeModal={activeModal}>
                    {
                        this._tags().map(value => this._tag_modal(value[0]))
                    }
                    <ModalCard
                        id={MODAL_CARD_PLAY_MODE}
                        onClose={() => this.setActiveModal(null)}
                        icon={<Icon56ServicesOutline/>}
                        header='Какую игру запускать?'
                        actionsLayout='horizontal'
                        actions={
                            [
                                <Button
                                    size='l'
                                    mode='gradient_blue'
                                    onClick={() => this.go('search')}
                                >
                                    Обычная
                                </Button>,
                                <Button
                                    size='l'
                                    mode='gradient_violet'
                                    onClick={() => this.go('friends')}
                                >
                                    С друзьями
                                </Button>
                            ]
                        }
                    ></ModalCard>
                    <ModalCard
                        id={MODAL_CARD_NEW_UPDATE}
                        onClose={() => this.setActiveModal(null)}
                        icon={<Icon56CakeCircleFillPurple/>}
                        header='Обновление'
                        subheader='Глобальные изменения, внесение поправок в правила игры.'
                        actions={
                            <Button
                                size='l'
                                mode='gradient_violet'
                                onClick={() => {
                                    bridge.send('VKWebAppStorageSet', {key: 'update_01062022', value: '1'});
                                    this.setActiveModal(null);
                                    openUrl('https://vk.com/@draw_app-update-01-06-2022');
                                }}
                            >
                                Читать
                            </Button>
                        }
                    ></ModalCard>
                    <ModalCard
                        id={MODAL_CARD_OUTDATED_VERSION}
                        onClose={() => this.setActiveModal(null)}
                        icon={<Icon56ErrorTriangleOutline fill='#E63902'/>}
                        header='Предупреждение'
                        subheader={`Вы используете устаревшую версию мини-приложения. ${isPlatformIOS() ? 'Обновите приложение ВКонтакте через App Store.' : 'Попробуйте почистить кэш (через три точки справа).'}`}
                    ></ModalCard>
                    <ModalPage
                        id={MODAL_PAGE_FRIEND_LOBBY_SETTINGS}
                        onClose={() => this.setActiveModal(null)}
                        dynamicContentHeight
                        header={
                            <ModalPageHeader
                                right={
                                    <PanelHeaderButton onClick={() => this.setActiveModal(null)}>
                                        <Icon24Dismiss/>
                                    </PanelHeaderButton>
                                }
                            >
                                Настройки
                            </ModalPageHeader>
                        }
                    >
                        <Div style={{paddingBottom: 24}}>
                            <div className='Span'>
                                Видимость лобби
                            </div>
                            <SegmentedControl
                                style={{marginTop: 8}}
                                value={friend_lobby.friendsOnly ? 'private' : 'public'}
                                onChange={value => {
                                    friend_lobby.friendsOnly = value === 'private';
                                    this.setState({friend_lobby});
                                    socket.call('friends.changeVisibility', {friendsOnly: friend_lobby.friendsOnly});
                                }}
                                options={[{label: 'Для всех', value: 'public'},
                                    {label: 'Для друзей', value: 'private'}]}
                            />
                            {
                                (user.premium || user.admin) &&
                                <React.Fragment>
                                    <div className='Span' style={{marginTop: 12}}>
                                        Максимальное кол-во игроков
                                    </div>
                                    <Input
                                        style={{marginTop: 8}}
                                        placeholder='Число (макс 500)' type='number'
                                        defaultValue={20} min={2} max={500}
                                        onChange={e => {
                                            socket.call('friends.changeMaxPlayers', {count: e.currentTarget.value});
                                        }}/>
                                </React.Fragment>
                            }
                            {
                                !friend_lobby.friendsOnly &&
                                <Button
                                    style={{
                                        marginTop: 12, width: '100%'
                                    }}
                                    before={<Icon16Share/>}
                                    size='m' mode='gradient_gray' stretched
                                    onClick={async () => {
                                        try {
                                            await bridge.send('VKWebAppShare', {link: `https://vk.com/drawapp#lobby${user.id}`});
                                        } catch (e) {
                                            bridge.send('VKWebAppCopyText', {text: `https://vk.com/drawapp#lobby${user.id}`});
                                            this.setActiveModal(null);
                                            this.setState({
                                                snackbar: <Snackbar
                                                    onClose={() => this.setState({snackbar: null})}>
                                                    Ссылка скопирована
                                                </Snackbar>
                                            });
                                        }
                                    }}
                                >
                                    Отправить приглашение
                                </Button>
                            }
                        </Div>
                    </ModalPage>
                </ModalRoot>
            )
        ;

        return (
            <SplitLayout
                popout={popout}
                modal={modal}
            >
                <SplitCol animate={!isPlatformDesktop()}>
                    <View
                        style={{
                            overflow: this.isNonScrollingElement(activePanel) && 'hidden'
                        }}
                        activePanel={(activePanel !== 'placeholder' && activePanel !== 'banned') ? (showConnecting ? 'connecting' : isShowOnboard ? 'onboard' : activePanel) : activePanel}
                        onSwipeBack={this.back}
                        history={this.state.history}
                    >
                        {
                            [
                                {
                                    id: 'onboard',
                                    component: <Placeholder
                                        icon={<IconOnboard/>}
                                        title='Добро пожаловать'
                                        description='Здесь Вы можете почувствовать себя в роли как художника, так и Шерлока. Рисуйте картины и угадывайте чужие.'
                                        buttonIcon={<Icon28ChevronRightOutline width={16} height={16}/>}
                                        buttonText='Продолжить'
                                        buttonOnClick={() => {
                                            bridge.send('VKWebAppStorageSet', {key: 'onboard', value: '1'});
                                            this.setState({isShowOnboard: false});
                                        }}
                                    />
                                },
                                {
                                    id: 'connecting',
                                    component: <Placeholder
                                        icon={<IconOnboard/>}
                                        title='Подключение'
                                        description='Выполняется подключение к серверу...'
                                    />
                                },
                                {
                                    id: 'placeholder',
                                    component: <Placeholder
                                        icon={<IconError/>}
                                        title='Соединение потеряно'
                                        description={placeholderText}
                                        buttonText='Переподключиться'
                                        buttonOnClick={() => {
                                            this.connect();
                                        }}
                                    />
                                },
                                {
                                    id: 'main',
                                    component: <React.Fragment>
                                        <Main t={this} time={this.state.time}/>
                                        {snackbar}
                                    </React.Fragment>
                                },
                                {
                                    id: 'shop',
                                    component: <React.Fragment>
                                        <Shop t={this} watchedShopItems={watchedShopItems}
                                              boughtShopItems={boughtShopItems}/>
                                        {snackbar}
                                    </React.Fragment>
                                },
                                {
                                    id: 'stats',
                                    component: <React.Fragment>
                                        <Stats t={this}/>
                                        {snackbar}
                                    </React.Fragment>
                                },
                                {
                                    id: 'gallery',
                                    component: <React.Fragment>
                                        <Gallery t={this}/>
                                        {snackbar}
                                    </React.Fragment>
                                },
                                {
                                    id: 'rating',
                                    component: <React.Fragment>
                                        <Rating t={this}/>
                                        {snackbar}
                                    </React.Fragment>
                                },
                                {
                                    id: 'likes',
                                    component: <React.Fragment>
                                        <Likes t={this}/>
                                        {snackbar}
                                    </React.Fragment>
                                },
                                {
                                    id: 'picture_info',
                                    component: <React.Fragment>
                                        <PictureInfo
                                            t={this}
                                            current={like_currentPictureSelected}
                                            method={like_method} param={like_param} user_id={like_user_id}
                                        />
                                        {snackbar}
                                    </React.Fragment>
                                },
                                {
                                    id: 'author',
                                    component: <Author t={this}/>
                                },
                                {
                                    id: 'search',
                                    component: <Search t={this}/>
                                },
                                {
                                    id: 'game',
                                    component: <React.Fragment>
                                        <Game t={this} drawerId={drawerId} lobbyId={lobbyId} online={isOnlineDrawing}
                                              word={word}/>
                                        {snackbar}
                                    </React.Fragment>
                                },
                                {
                                    id: 'global_canvas',
                                    component: <React.Fragment>
                                        <GlobalCanvas t={this}/>
                                        {snackbar}
                                    </React.Fragment>
                                },
                                {
                                    id: 'global_canvas_info',
                                    component: <React.Fragment>
                                        <GlobalCanvasInfo t={this}/>
                                        {snackbar}
                                    </React.Fragment>
                                },
                                {
                                    id: 'result',
                                    component: <React.Fragment>
                                        <Result t={this} drawerId={drawerId} winnerId={winnerId} word={word}
                                                gameId={gameId}/>
                                        {snackbar}
                                    </React.Fragment>
                                },
                                {
                                    id: 'tags',
                                    component: <Tags t={this}/>
                                },
                                {
                                    id: 'friends',
                                    component: <React.Fragment>
                                        <Friends t={this}/>
                                        {snackbar}
                                    </React.Fragment>
                                },
                                {
                                    id: 'friend_lobby',
                                    component: <React.Fragment>
                                        <FriendLobby t={this} friend_lobby={friend_lobby}/>
                                        {snackbar}
                                    </React.Fragment>
                                },
                                {
                                    id: 'group_vk',
                                    component: <Placeholder
                                        icon={<img src={require('../../assets/drawing/icons/Icon_Group.png')}
                                                   alt='icon'/>}
                                        title='С возвращением!'
                                        description='Не хочешь вступить в наше сообщество ВКонтакте? Именно там публикуются все крутые обновления'
                                        buttonText='Вступить'
                                        buttonOnClick={async () => {
                                            bridge.send('VKWebAppStorageSet', {key: 'vk_group1', value: '1'});
                                            this.setState({isShowVkGroup: false});
                                            try {
                                                await bridge.send('VKWebAppJoinGroup', {group_id: 208964042});
                                                adAppApi('stats.subscribe', {group_id: 208964042});
                                            } catch (e) {
                                            }
                                            this.back();
                                        }}
                                        buttonMode='gradient_gray'
                                        buttonBack={true}
                                        t={this}
                                    />
                                },
                                {
                                    id: 'vk_donut',
                                    component: <Placeholder
                                        icon={<img src={require('../../assets/drawing/icons/Icon_Donut.png')}
                                                   alt='icon'/>}
                                        title='VK Donut'
                                        description='Чтобы получить доступ к дополнительным функциям, оформите подписку.'
                                        buttonText='Перейти'
                                        buttonOnClick={() => {
                                            openUrl('https://vk.com/donut/draw_app');
                                        }}
                                        buttonMode='gradient_donut'
                                        buttonBack={true}
                                        t={this}
                                    />
                                },
                                {
                                    id: 'vk_donut_ios',
                                    component: <Placeholder
                                        icon={<img src={require('../../assets/drawing/icons/Icon_Donut.png')}
                                                   alt='icon'/>}
                                        title='VK Donut'
                                        description='Для уточнения информации, напишите нам в группу ВКонтакте.'
                                        buttonText='Написать'
                                        buttonOnClick={() => {
                                            openUrl('https://vk.com/draw_app');
                                        }}
                                        buttonMode='gradient_donut'
                                        buttonBack={true}
                                        t={this}
                                    />
                                },
                                {
                                    id: 'banned',
                                    component: <Placeholder
                                        icon={<img src={require('../../assets/drawing/icons/Icon_Group.png')}
                                                   alt='icon'/>}
                                        title='Блокировка'
                                        description={`Вы были заблокированы за нарушение правил. Дата разблокировки: ${new Date(banned + (24 * 60 * 60 * 1000)).toLocaleDateString('ru', {
                                            day: 'numeric',
                                            month: 'numeric',
                                            year: 'numeric',
                                            hour: 'numeric',
                                            minute: 'numeric'
                                        })}`}
                                        buttonText='Перейти в группу'
                                        buttonOnClick={() => {
                                            openUrl('https://vk.com/club' + 208964042);
                                        }}
                                        buttonMode='gradient_gray'
                                        t={this}
                                    />
                                },
                            ].map(({id, component}, index) =>
                                <Panel id={id} key={`Panel__${index}`}>
                                    {component}
                                </Panel>
                            )
                        }
                    </View>
                </SplitCol>
            </SplitLayout>
        );
    }
}

export default Drawing;