import React, {PureComponent} from 'react';
import PropTypes from 'prop-types';
import {
    Avatar,
    Button,
    IconButton,
    Input,
    PanelHeader,
    PanelHeaderBack, PromoBanner,
    ScreenSpinner,
    Snackbar,
    WriteBarIcon
} from "@vkontakte/vkui";
import bridge from "@vkontakte/vk-bridge";
import {
    isPlatformDesktop
} from "../../js/utils";
import {ReactComponent as IconErase2} from "../../assets/drawing/icons/IconErase2.svg";
import {Icon28CompassOutline, Icon28Crop, Icon28MailOutline, Icon28PaletteOutline} from "@vkontakte/icons";

const isDesktop = isPlatformDesktop();

export class GlobalCanvasInfo extends PureComponent {

    render() {
        const {t} = this.props;

        return (
            <React.Fragment>
                <PanelHeader
                    left={<PanelHeaderBack onClick={t.back}/>}
                    separator={false}
                >
                    {
                        isDesktop && 'Общий холст'
                    }
                </PanelHeader>
                <div className='GlobalCanvasInfo' style={{paddingBottom: 24}}>
                    {
                        !isDesktop && <h2 style={{textAlign: 'left'}}>Общий холст</h2>
                    }
                    <p style={{margin: !isDesktop && '8px 0 0 0'}}>
                        На общем холсте могут рисовать все игроки. Рисовать можно на определённой области. Всего таких
                        областей 25.
                    </p>
                    <div className='GlobalCanvas_Items__List'>
                        {
                            [
                                [<Icon28CompassOutline width={20} height={20}/>, 'Перемещение по холсту'],
                                [<Icon28PaletteOutline width={20} height={20}/>, 'Меню палитры'],
                                [<IconErase2 />, 'Ластик позволяет стирать рисунок в определённом месте'],
                                [<Icon28Crop width={20} height={20}/>, 'Показать весь холст'],
                                [<Icon28MailOutline width={20} height={20}/>, 'Открыть чат']
                            ].map((value, index) =>
                                <div key={`item_${index}`}>{value[0]}<span>{value[1]}</span></div>
                            )
                        }
                    </div>
                    <p style={{margin: '18px 0 0 0'}}>
                        Свободную область можно арендовать (1000 монет на час). На арендованной области может рисовать
                        только владелец. Продлить аренду невозможно — можно только купить её снова по окончанию срока.
                    </p>
                </div>
            </React.Fragment>
        )
    }
}

GlobalCanvasInfo.defaultProps = {};

GlobalCanvasInfo.propTypes = {
    t: PropTypes.object
};

export default GlobalCanvasInfo;